import React, { lazy, useEffect, useState } from 'react'
import Select from 'react-select';
import { useHistory } from "react-router-dom";
import {
  CCol,
  CSelect,
  CCollapse,
  CCard,
  CLink,
  CModal,
  CFormGroup,
  CInputCheckbox,
  CButton,
  CLabel,
  CInput,
  CProgress,
  CRow,
  CCallout,
  CDataTable,
  CTextarea,
  CModalHeader,
  CModalBody,
  CModalFooter,
  CForm,
  CCardBody
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {subkomoditasgaram, subsubkomoditasgaram, stdmutugaram, hscodegaram, subkomoditasberas,subsubkomoditasberas,stdmutuberas,hscodeberas, subkomoditasikan, subsubkomoditasikan, stdmutuikan, hscodeikan} from './sumber.js'
import { useSelector } from 'react-redux';
import store from '../../redux/store'
import { text } from '@fortawesome/fontawesome-svg-core';
import Swal from 'sweetalert2';

const FormImpor = (props) => {
    const history = useHistory();
    // useEffect(() => {
    //     if(histori.length = 1){
    //         var kopidata = histori.slice()
    //         kopidata[0].timestamp = Date().replace(/^\S+\s/,'')
    //         store.dispatch({ type: 'histori/updateHistori', payload: kopidata })
    //     }
    //     console.log(histori)
    //   },[]);     
    const [details, setDetails] = useState([])
    const toggleDetails = (index) => {
        const posisi = details.indexOf(index)
        let newDetails = details.slice()
        if(posisi !== -1){
          newDetails.splice(posisi, 1)
        } else {
          newDetails = [...details, index]
        }
        setDetails(newDetails)        
    }
    const histori = useSelector(state=>state.histori)
    const data = useSelector(state=>state.status)
    const rolemendag = useSelector(state=>state.rolemendag)
    const state = useSelector(state=>state)
    const [change, setChange] = useState("1")
    const [textarea, setTextarea] = useState()
    const handleTextarea = (e) =>{
        setTextarea(e.target.value)
    }
    const handleChange = (e) =>{
        setChange(e.target.value)
    }
    const handleClick = () =>{
        if (change == "1"){
            var kopidata = data.slice()
            kopidata[0].kdstatus = parseInt(rolemendag)+1
            store.dispatch({ type: 'status/updateStatus', payload: kopidata })
            var payload = {}
            payload.status = parseInt(rolemendag) + 1
            payload.changedby = rolemendag
            payload.timestamp = Date().replace(/^\S+\s/,'')
            payload.komentar = textarea
            var kopihistori = histori.slice()
            kopihistori = [...kopihistori, payload]
            store.dispatch({ type: 'histori/updateHistori', payload: kopihistori })
            console.log(state)
            history.push('/inhousemendag')
        } else if (change == "2") {
            var kopidata = data.slice()
            kopidata[0].kdstatus = parseInt(rolemendag)-1
            store.dispatch({ type: 'status/updateStatus', payload: kopidata })
            var payload = {}
            payload.status = parseInt(rolemendag) - 1
            payload.changedby = rolemendag
            payload.timestamp = Date().replace(/^\S+\s/,'')
            payload.komentar = textarea
            var kopihistori = histori.slice()
            kopihistori = [...kopihistori, payload]
            store.dispatch({ type: 'histori/updateHistori', payload: kopihistori })
            console.log(state)
            history.push('/inhousemendag')
        }
    }
  return (
      <>
      <CCard>
          <CCardBody>
              <div>
                  <h5>Histori Keputusan</h5>
              </div>
              <hr/>
              <CDataTable
              addTableClasses="josss"
              items={histori}
              fields={[
                { key: 'no',  label:"No" },      
                { key: 'status', label:"Status" },
                { key: 'changedby',  label:"Changed By" },      
                { key: 'timestamp',  label:"Timestamp" },
                { key: 'komentar',  label:"Komentar" },  
              ]}
              scopedSlots={{
                'details':
                (item, index)=>{
                  return(
                    <div>
                      <CCollapse
                      show={details.includes(index)}
                      >
                        <CTextarea disabled value={item.komentar}></CTextarea>
                      </CCollapse>                      
                    </div>
                  )
                },
                'komentar':
                (item,index)=>{
                  return(
                    <td>
                      <CButton onClick={()=>toggleDetails(index)} className="btn btn-sm" style={{backgroundColor:"#02265d", color:"white"}}>Lihat</CButton>
                    </td>
                  )
                },
                'changedby':
                (item)=>{
                    var user
                    if (item.changedby == 0){
                        user = "Sistem"
                    } else if (item.changedby == 1){
                        user = "User Pendok"
                    } else if(item.changedby == 2){
                        user = "User Kasi"
                    } else if (item.changedby == 3){
                        user = "User Kasubdiv"
                    } else if(item.changedby == 4){
                        user = "User Direktur"
                    } else if (item.changedby == 5){
                        user = "User Dirjen"
                    }
                    return(
                        <td>
                            {user}
                        </td>
                    )
                },
                'no':
                (item,index)=>{
                    index += 1
                    return(
                        <td>
                            {index}
                        </td>
                    )
                },
                'status':
                (item)=>{
                    var status
                    console.log(item)
                    if(item.status == 1){
                        status = "Penerimaan Dokumen"
                    } else if (item.status == 2){
                        status = "Penelitian Kasi"
                    } else if (item.status == 3){
                        status = "Penelitian Kasubdiv"
                    } else if (item.status == 4){
                        status = "Penelitian Direktur"
                    } else if (item.status == 5){
                        status = "Penelitian Dirjen"
                    } else {
                        status = "Penerbitan Perijinan"
                    }
                    return(
                        <td>
                            {status}
                        </td>
                    )
                }
            }}              
              />
          </CCardBody>
      </CCard>
      <CCard>
          <CCardBody>
              <div><h5>Pemberian Keputusan</h5></div>
              <hr/>
              <div clsssName="d-flex flex-row">
                  <CFormGroup>
                      <CLabel>Pilih Keputusan</CLabel>
                      <CSelect onChange={(e)=>handleChange(e)}>
                          <option value="1">Setujui</option>
                          <option value="2">Kembalikan</option>
                      </CSelect>
                  </CFormGroup>
              </div>
              <div>
                  <CFormGroup>
                      <CLabel>Komentar : </CLabel>
                  <CTextarea onChange={(e)=>handleTextarea(e)}>
                  </CTextarea>
                  </CFormGroup>
              </div>
              <div className="d-flex flex-row justify-content-end pt-3">
                  <CLink role="button" onClick={()=>handleClick()} className="btn btn-sm btn-success">Kirim Keputusan</CLink>
              </div>
          </CCardBody>
      </CCard>
      </>
  )
}

export default FormImpor
