import React, { lazy, useEffect, useState } from 'react'
import Select from 'react-select';
import {
  CCol,
  CSelect,
  CCollapse,
  CTooltip,
  CModal,
  CFormGroup,
  CInputCheckbox,
  CButton,
  CLabel,
  CInput,
  CProgress,
  CRow,
  CCallout,
  CDataTable,
  CTextarea,
  CModalHeader,
  CModalBody,
  CModalFooter,
  CForm
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {subkomoditasgaram, subsubkomoditasgaram, stdmutugaram, hscodegaram, subkomoditasberas,subsubkomoditasberas,stdmutuberas,hscodeberas, subkomoditasikan, subsubkomoditasikan, stdmutuikan, hscodeikan} from './sumber.js'
import { useSelector } from 'react-redux';
import store from '../../redux/store'

const FormImpor = (props) => {
    const PI = useSelector(state=>state.PI)
    const [dataitem, setDataitem]=useState([])
    useEffect(() => {
        // Update the document title using the browser API
            setDataitem([
                {
                    "no":1,
                    "nmdok":"Izin Usaha Industri",
                    "nodok":"",
                    "status":"Selesai"
                },
                {
                    "no":2,
                    "nmdok":"Surat pernyataan bermeterai cukup keterangan mengenai rencana Impor Garam",
                    "nodok":"",
                    "status":"Selesai"
                },  
                {
                    "no":3,
                    "nmdok":"Rencana Penyaluran/Distribusi/Penjualan produk yang diolah menggunakan garam",
                    "nodok":"",
                    "status":"Selesai"
                },
                {
                    "no":4,
                    "nmdok":"NIB",
                    "nodok":"",
                    "status":"Selesai"
                },
                {
                    "no":5,
                    "nmdok":"Surat Izin Usaha Perdagangan",
                    "nodok":"",
                    "status":"Selesai"
                },                                                
            ])
      },[]);
      const handleMenu = (e) =>{
        store.dispatch({ type: 'menu/changeMenu', payload: e })
      }
      const [disabled, setDisabled] = useState(false)
      const [salah, setSalah]= useState(false)
      const handleNo = () => {
          setSalah(!salah)
          setDisabled(!disabled)
      }    
      const [modalhistory, setModalhistory] = useState(false)   
  return (
      <>
                    <>
                    <CModal
    show={modalhistory}
    onClose={()=>setModalhistory(false)}
    >
      <CModalHeader>
        <h5>Histori Catatan</h5>
      </CModalHeader>
      <CModalBody>
        <CDataTable
        addTableClasses="josss"
        items={[
          {
            no:1,
            user:"User Kasi",
            catatan:"Data NIB Tidak Mutakhir"
          }
        ]}
        fields={[
          { key: 'no', label:"Nomor" },
          { key: 'user',  label:"User" },
          { key: 'catatan',  label:"Catatan" },
        ]}
        />
      </CModalBody>
    </CModal>                    
                    <div className="d-flex justify-content-end">
                        <div className="px-2">
                            <h4><i>Apakah Ada Ketidaksesuaian ?</i></h4>
                        </div>
                        <div className="px-2">
                            <CButton onClick={()=>handleNo()} className="btn btn-success">{salah == true ? "Tidak" : "Ya"}</CButton>
                        </div>
                        <div className="px-2">
                            <CButton onClick={()=>setModalhistory(true)} className="btn btn-warning">Histori Catatan</CButton>
                        </div>                        
                    </div>
                    <hr/>
                    <CCollapse show={disabled}>
                        <CLabel><h5>Catatan :</h5></CLabel>
                        <CTextarea></CTextarea>
                        <hr/>                     
                    </CCollapse>                    
                    </>        
      <div>
          <h5>{PI}</h5>
      </div>
        <div>
        <CDataTable
        addTableClasses="josss"
        items={dataitem}
        fields={[
        { key: 'no',  label:"No" },      
        { key: 'nmdok', label:"Nama Persyaratan" },
        // { key: 'nodok',  label:"Nomor" },      
        { key: 'status',  label:"Status" },
        { key: 'aksi',  label:"Aksi" },              
        ]}
        scopedSlots={{
            'aksi':
            (item)=>{
                var content
                var menu
                if(item.no == 1){
                    content="Cek Profil Pelaku Usaha"
                    menu = "profil"
                } else if (item.no == 2){
                    content="Cek Disclaimer Checkpoint"
                    menu = "checkpoint"
                } else if (item.no == 3) {
                    content="Cek Hasil Verifikasi"
                    menu = "LHV"
                } else if(item.no == 4) {
                    content="Cek Profil Pelaku Usaha"
                    menu = "profil"
                } else {
                    content="Cek Profil Pelaku Usaha"
                    menu = "profil"
                }
                return(
                    <td>
                        <CTooltip content={content}>
                            <CButton onClick={()=>handleMenu(menu)} className="btn btn-sm" style={{backgroundColor:"#02265d", color:"white"}}><i class="fas fa-info-circle"></i></CButton>
                        </CTooltip>                        
                    </td>
                )
            }
        }}
        />
        </div>
      </>
  )
}

export default FormImpor
